.page-resources{
  h1{
    text-align:center;
    font-size:rem-calc(50);
    font-style:italic;
    margin:60px 0 0 0;
  }
  ul.filters{
    list-style:none;
    margin:35px 0;
    text-align:center;
    text-transform:uppercase;
    font-size:1rem;
    li{
      display:inline-block;
      padding:0 10px;
      &:not(:last-child){
        border-right:1px solid $medium-gray;
      }
      &.highlight{
        color:$orange;
      }
    }
  }
  section#articles{
    @include grid-row();
  }
  article{
    margin-bottom:20px;
    @include grid-column(12);
    @include breakpoint(medium){
      @include grid-column(6);
      margin-bottom:30px;
    }
    @include breakpoint(large){
      @include grid-column(3);
    }
    .wp-post-image{
      width:100%;
      height:auto;
    }
    div.content{
      padding: 0 20px 20px 20px;
    }
    h2{
      font-size:26px;
      margin: 0 0 15px 0;
    }
    p.article-details{
      display:inline-block;
      font-size:12px;
      margin:20px 0;
    }

    div.article-body{
      background-color:#a6a6a6;
      color:$white;
    }
    &.whitepapers div.article-body{
      background-color:$navy;
    }
    &.resources div.article-body{
      background-color:$white;
      color:$navy;
    }
    &.highlight div.article-body, &.events div.article-body{
      background-color:$orange;
    }
  }
}
